@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600;700&family=Poppins:wght@300;400;600;800&display=swap');

body {
    font-family: 'Poppins', sans-serif;
    background-color: #fff0f3;
    background-image: radial-gradient(#ffccd5 1px, transparent 1px);
    background-size: 20px 20px;
}

h1, h2, h3, .brand-font {
    font-family: 'Quicksand', sans-serif;
}

.glass-panel {
    background: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border: 1px solid rgba(255, 255, 255, 0.9);
}

.card-hover {
    transition: all 0.3s ease;
}
.card-hover:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.btn-gradient {
    background: linear-gradient(135deg, #e11d48 0%, #be123c 100%);
}

.blob {
    position: absolute;
    filter: blur(40px);
    z-index: -1;
    opacity: 0.6;
    animation: float 10s infinite ease-in-out;
}

@keyframes float {
    0% { transform: translate(0px, 0px) scale(1); }
    33% { transform: translate(30px, -50px) scale(1.1); }
    66% { transform: translate(-20px, 20px) scale(0.9); }
    100% { transform: translate(0px, 0px) scale(1); }
}

::-webkit-scrollbar {
    width: 8px;
}
::-webkit-scrollbar-track {
    background: #fff0f3;
}
::-webkit-scrollbar-thumb {
    background: #fb7185;
    border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
    background: #e11d48;
}

.filtro-anim-container {
  height: 0;
  opacity: 0;
  pointer-events: none;
}
.filtro-anim-container.visible {
  pointer-events: auto;
}
.filtro-anim-container.activo {
  height: 420px; /* ajusta seg√∫n el contenido */
  opacity: 1;
}

.card-migajero {
  transition:
    transform 0.22s cubic-bezier(.4,0,.2,1),
    box-shadow 0.22s cubic-bezier(.4,0,.2,1);
  box-shadow: 0 2px 8px rgba(220,38,120,0.07), 0 1.5px 6px rgba(0,0,0,0.04);
  will-change: transform, box-shadow;
  background: #fff;
  position: relative;
  z-index: 1;
}
.card-migajero:hover {
  transform: translateY(-12px) scale(1.025);
  box-shadow:
    0 24px 48px -12px rgba(220,38,120,0.18),
    0 8px 32px -8px rgba(0,0,0,0.12),
    0 2px 8px rgba(220,38,120,0.10);
  /* efecto de brillo sutil */
  border-color: #f43f5e;
}
.card-migajero::after {
  content: "";
  display: block;
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: -18px;
  height: 18px;
  border-radius: 50%;
  background: radial-gradient(ellipse at center, rgba(220,38,120,0.10) 0%, rgba(220,38,120,0.00) 80%);
  opacity: 0;
  transition: opacity 0.22s;
  z-index: 0;
}
.card-migajero:hover::after {
  opacity: 1;
}
